@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;500;600;700&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  font-family: 'Josefin Sans', sans-serif;;
  color: #61dafb;
}

h1{
  font-size: 4rem;
}

p{
  font-size: 2rem;
}

.navbar{
  background-color: #4c4c4c;
  color: #61dafb;
}

a{
  text-decoration: none;
  color: #61dafb;
  margin-right: 1rem;
}

.navbar{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar h1{
  margin-left: 5rem;
}

.navbar .link {
  margin-right: 5rem;
}

.navbar a:hover{
  font-size: 1.5rem;
}

#about-me{
  height: 100%;
  background-color: #0c0c0c;
  padding: 15rem 5rem 25rem 5rem;

}

#competence{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #343434;
}

.wrapper{
  height: 100%;

}

.flex{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  position:sticky;
  margin: 0px 2rem 0px 5rem;
  border-right:  5px solid #61dafb ;
}

.competence{
  display: grid;
  grid-template-columns: repeat(3, 1fr);

}

.competence img{
  width: 100%;
}

#competence h2{
  width: 100%;
  border-bottom: 3px solid #61dafb;
}

.skill{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#project{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #0c0c0c;
}

.project_wrapper{
  height: 100%;
}

.project_flex{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  position:sticky;
  margin: 0px 2rem 0px 2rem;
  border-right:  5px solid #61dafb ;
}

.project img {
  width: 100%;
}

.dealgames{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.description p{
  font-size: 1.5rem;
}

.description {
  margin-bottom: 2rem;
}

.description a {
  text-decoration: underline;
  font-size: 1.5rem;
  margin-left: 45%;
}

#network{
  background-color: #4c4c4c ;
}

.network {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 12rem;
}

.network img {
  width: 80%;
}

#network h1{
  margin-bottom: 0;
}

.network_flex{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  position:sticky;
  margin: 0 0 0 0;

}